.login-container{
    margin-top: 5%;
    margin-bottom: 5%;
}
.login-logo{
    position: relative;
    margin-left: -41.5%;
}

.login-form-1{
    padding: 9%;
    
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
}
.login-form-1 h3{
    text-align: center;
    margin-bottom:12%;
    color:#fff;
}

.btnSubmit{
    font-weight: 600;
    width: 50%;
    color: #282726;
    background-color: #fff;
    border: none;
    border-radius: 1.5rem;
    padding:2%;
}
.btnForgetPwd{
    color: #fff;
    font-weight: 600;
    text-decoration: none;
    cursor: pointer;
}
.btnForgetPwd:hover{
    text-decoration:none;
    color:#fff;
}

#errorForLogIn {
    margin-bottom: 36px;
    color:#FF7733;
}

#passWordResetSuccess {
    color: #61FF7E;
    margin-bottom: 36px;
}

#signUpSucess {
    color: #61FF7E;
    margin-bottom: 37px;
}